    sendPasswordResetEmail(email: string) {
        var auth = firebase.auth();
        auth.sendPasswordResetEmail(email).then(function () {
            alert(email + '     .')
        }).catch((error) => {
            console.log(error);
            alert(this.convertErrorMessage(error));
        });
    }
    convertErrorMessage(error: any) {
        const errorMessage: any = {
            'auth/invalid-email': '  .',
            'auth/invalidNewPw': '   .',
            'auth/weak-password': '6   ',
            'auth/wrong-password': '  .',
            'auth/user-not-found': '  .     .',
            'auth/email-already-in-use': '  .',
        }
        const convertMessage = errorMessage[error.code]
        if (convertMessage) {
            return convertMessage;
        }
        console.log(error);
        return error.message;
    }
